ndex';\n@import './mixin';\n\n@btn-prefix-cls: ~'@{ant-prefix}-btn';\n\n// for compatible\n@btn-ghost-color: @text-color;\n@btn-ghost-bg: transparent;\n@btn-ghost-border: @border-color-base;\n\n// Button styles\n// -----------------------------\n.@{btn-prefix-cls} {\n  // Fixing https://github.com/ant-design/ant-design/issues/12978\n  // Fixing https://github.com/ant-design/ant-design/issues/20058\n  // Fixing https://github.com/ant-design/ant-design/issues/19972\n  // Fixing https://github.com/ant-design/ant-design/issues/18107\n  // Fixing https://github.com/ant-design/ant-design/issues/13214\n  // It is a render problem of chrome, which is only happened in the codesandbox demo\n  // 0.001px solution works and I don't why\n  line-height: @btn-line-height;\n  .btn();\n  .btn-default();\n\n  // Fix loading button animation\n  // https://github.com/ant-design/ant-design/issues/24323\n  > span {\n    display: inline-block;\n  }\n\n  &-primary {\n    .btn-primary();\n\n    .@{btn-prefix-cls}-group &:not(:first-child):not(:last-child) {\n      border-right-color: @btn-group-border;\n      border-left-color: @btn-group-border;\n\n      &:disabled {\n        border-color: @btn-default-border;\n      }\n    }\n\n    .@{btn-prefix-cls}-group &:first-child {\n      &:not(:last-child) {\n        border-right-color: @btn-group-border;\n\n        &[disabled] {\n          border-right-color: @btn-default-border;\n        }\n      }\n    }\n\n    .@{btn-prefix-cls}-group &:last-child:not(:first-child),\n    .@{btn-prefix-cls}-group & + & {\n      border-left-color: @btn-group-border;\n\n      &[disabled] {\n        border-left-color: @btn-default-border;\n      }\n    }\n  }\n\n  &-ghost {\n    .btn-ghost();\n  }\n\n  &-dashed {\n    .btn-dashed();\n  }\n\n  // type=\"danger\" will deprecated\n  // use danger instead\n  &-danger {\n    .btn-danger();\n  }\n\n  &-link {\n    .btn-link();\n  }\n\n  &-text {\n    .btn-text();\n  }\n\n  &-dangerous {\n    .btn-danger-default();\n  }\n\n  &-dangerous&-primary {\n    .btn-danger();\n  }\n\n  &-dangerous&-link {\n    .btn-danger-link();\n  }\n\n  &-dangerous&-text {\n    .btn-danger-text();\n  }\n\n  &-icon-only {\n    .btn-square(@btn-prefix-cls);\n    vertical-align: -1px;\n  }\n\n  &-round {\n    .btn-round(@btn-prefix-cls);\n    &.@{btn-prefix-cls}-icon-only {\n      width: auto;\n    }\n  }\n\n  &-circle {\n    .btn-circle(@btn-prefix-cls);\n  }\n\n  &::before {\n    position: absolute;\n    top: -@btn-border-width;\n    right: -@btn-border-width;\n    bottom: -@btn-border-width;\n    left: -@btn-border-width;\n    z-index: 1;\n    display: none;\n    background: @component-background;\n    border-radius: inherit;\n    opacity: 0.35;\n    transition: opacity 0.2s;\n    content: '';\n    pointer-events: none;\n  }\n\n  .@{iconfont-css-prefix} {\n    transition: margin-left 0.3s @ease-in-out;\n\n    // Follow icon blur under windows. Change the render.\n    // https://github.com/ant-design/ant-design/issues/13924\n    &.@{iconfont-css-prefix}-plus,\n    &.@{iconfont-css-prefix}-minus {\n      > svg {\n        shape-rendering: optimizeSpeed;\n      }\n    }\n  }\n\n  &&-loading {\n    position: relative;\n    &:not([disabled]) {\n      pointer-events: none;\n    }\n\n    &::before {\n      display: block;\n    }\n  }\n\n  & > &-loading-icon {\n    transition: all 0.3s @ease-in-out;\n\n    .@{iconfont-css-prefix} {\n      padding-right: @padding-xs;\n      animation: none;\n      // for smooth button padding transition\n      svg {\n        animation: loadingCircle 1s infinite linear;\n      }\n    }\n\n    &:only-child {\n      .@{iconfont-css-prefix} {\n        padding-right: 0;\n      }\n    }\n  }\n\n  &-group {\n    .btn-group(@btn-prefix-cls);\n  }\n\n  // http://stackoverflow.com/a/21281554/3040605\n  &:focus > span,\n  &:active > span {\n    position: relative;\n  }\n\n  // To ensure that a space will be placed between character and `Icon`.\n  > .@{iconfont-css-prefix} + span,\n  > span +